import { NotificationService } from './../../../../../shared/services/notification.service';
import { HttpClient } from '@angular/common/http';
import { Router, ActivatedRoute } from '@angular/router';
import { HeaderService } from './../../../../../shared/services/header.service';
import { GlobalService } from './../../../../../shared/services/global.service';
import { NgForm } from '@angular/forms';
import { Component, OnInit, ViewChild, OnDestroy } from '@angular/core';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-upload-profile-img',
  templateUrl: './upload-profile-img.component.html',
  styleUrls: ['./upload-profile-img.component.scss']
})
export class UploadProfileImgComponent implements OnInit, OnDestroy {
  LoggedParams: any;
  subscription1: Subscription;
  messageToDisplay: string;
  ErrorMsqBlock = false;
  successMsqBlock = false;
  image: any;
  imagename;
  profileSnap: any;
  editableData: any;
  public isLoading = false;
  @ViewChild('formData', {static: false}) imageUploadForm: NgForm;
  constructor(
    private globalService: GlobalService,
    private headerService: HeaderService,
    private router: Router,
    private route: ActivatedRoute,
    private http: HttpClient,
    private notificationService: NotificationService
  ) {
  }
  autoCloseMsqBlock() {
    setTimeout(() => {
      this.successMsqBlock = false;
      this.ErrorMsqBlock = false;
      this.messageToDisplay = '';
    }, 10000);
  }
  onCloseMsqBlock() {
    this.successMsqBlock = false;
    this.ErrorMsqBlock = false;
    this.messageToDisplay = '';
  }
  onSuccessMsqDisplay(msq: string) {
    this.messageToDisplay = '';
    this.ErrorMsqBlock = false;
    this.successMsqBlock = true;
    this.messageToDisplay = msq;
  }
  onErrorMsqDisplay(msq: string) {
    this.messageToDisplay = '';
    this.successMsqBlock = false;
    this.ErrorMsqBlock = true;
    this.messageToDisplay = msq;
  }
  fileEvent(fileInput: Event) {
    this.imagename = fileInput;
    console.log(fileInput);
    this.imagename = (fileInput.target as HTMLInputElement).files[0].name;
    console.log(this.imagename);
}

  ngOnInit(): void {
    this.loadDatas();
  }
  loadDatas() {
    this.subscription1 = this.headerService.castLoggedUserDetail.subscribe(
      resData => (this.LoggedParams = resData)
    );
    this.editableData = this.LoggedParams.sProfile;
  }
  updateLocalStorageData() {
    this.headerService.updateLocalStorageSProfile(this.editableData);
  }
   encodeImageFileAsURL(element: HTMLInputElement) {
      this.isLoading = true;
      const file = element.files[0];
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => {
      // console.log('RESULT', reader.result);
      const data = {
        App: 'uploadReceit',
        oData: reader.result
      };
      // tslint:disable-next-line: max-line-length
      this.http.post('http://client.traderscabinet.com/tr_test/assets/PHP/alfa/v1/common/UploadDipositPaymentReceipt', data).subscribe((res: any) => {
        this.profileSnap = res.resp.url;
        this.editableData.uPersonalInfo.ProfilePic = this.profileSnap;
        // tslint:disable-next-line: no-shadowed-variable
        this.globalService.edit(this.editableData).subscribe((res: any) => {
          if (res.Result === true) {
            // this.onSuccessMsqDisplay('Profile Picture Updated Successfully');
            // console.log('success');
            this.notificationService.showSuccess('Profile Picture Updated Successfully', 'Success');
            this.updateLocalStorageData();
            this.isLoading = false;
            setTimeout(() => {
              this.router.navigate(['../view'], {relativeTo: this.route});
             }, 1000);
          }
        }, err => {
          console.log(err);
          // this.onErrorMsqDisplay('Faild to Update Profile Picture');
          this.notificationService.showError('Faild to Update Profile Picture', 'Error');
          this.isLoading = false;
        });
      });
    };
  }
  ngOnDestroy() {
    this.subscription1.unsubscribe();
  }

}
