import { HeaderService } from './../../../../shared/services/header.service';
import { GlobalService } from './../../../../shared/services/global.service';
import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-profile-core',
  templateUrl: './profile-core.component.html',
  styleUrls: ['./profile-core.component.scss']
})
export class ProfileCoreComponent implements OnInit, OnDestroy {
  public walletBalance: any;
  public profileData: any;
  public paymentMethodAll: any;
  public minWithDraw: number;
  public minDeposit: number;
  isLoading: false;
  subscription1: Subscription;
  subscription2: Subscription;
  subscription3: Subscription;
  constructor(
    private globalService: GlobalService,
    private headerService: HeaderService
  ) { }

  ngOnInit(): void {
    this.loadDatas();
  }
  loadDatas() {
    this.subscription1 = this.headerService.castLoggedUserDetail.subscribe(
      resData => (this.profileData = resData)
    );
    console.log(this.profileData);
    this.subscription2 = this.headerService.castWalletBalance.subscribe(
      resData => (this.walletBalance = resData)
    );
    const parems = {
      DealerId: this.profileData.sProfile.sDealerId,
    };
    this.subscription3 =  this.globalService.getManulGateway(parems).subscribe((res: any) => {
      this.paymentMethodAll = res;
      this.minWithDraw = this.paymentMethodAll[0].Min_Withdraw;
      this.minDeposit = this.paymentMethodAll[0].Min_Depoist;
      }, err => {
        console.log(err);
      });
  }
  ngOnDestroy() {
    this.subscription1.unsubscribe();
    this.subscription2.unsubscribe();
    this.subscription3.unsubscribe();
  }
}
