import { UploadProfileImgComponent } from './profile-core/upload-profile-img/upload-profile-img.component';
import { ChangePasswordComponent } from './profile-core/change-password/change-password.component';
import { EditProfileComponent } from './profile-core/edit-profile/edit-profile.component';
import { ViewProfileComponent } from './profile-core/view-profile/view-profile.component';
import { ProfileCoreComponent } from './profile-core/profile-core.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


const routes: Routes = [
  {
    path: '',
    component: ProfileCoreComponent,
    children: [
      {
        path: '',
        pathMatch: 'full',
        redirectTo: 'view'
      },
      {
        path: 'view',
        component: ViewProfileComponent,
        data: {title: 'My Profile', breadcrumb: 'View Profile'}
      },
      {
        path: 'upload-profile-image',
        component: UploadProfileImgComponent,
        data: {title: 'My Profile', breadcrumb: 'Upload Profile image'}
      },
      {
        path: 'edit',
        component: EditProfileComponent,
        data: {title: 'My Profile', breadcrumb: 'Edit Profile'}
      },
      {
        path: 'change-password',
        component: ChangePasswordComponent,
        data: {title: 'My Profile', breadcrumb: 'Change Password'}
      }
    ]
  }
];
@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ProfileRoutingModule { }
